#homeBackground {
  height: 1005px;
  background-image: url("../../images/FullSize/Garage.JPG");
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: 1% 15%;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0;
  padding: 0;
  border: 0;
}

.header {
  padding-top: 150px;
  text-align: center;
  vertical-align: middle;
  font-size: 150px;
  color: white;
}

.arrow {
  text-align: center;
  margin-top: 5%;
}
.bounce {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

.player-wrapper {
  position: relative;
  padding-top: 50.2%; /* Player ratio: 100 / (1280 / 720) */
  margin-bottom: 0;
}

.react-player {
  position: absolute;
  top: 0;
  left: 0;
}
#bounceArrow2 {
  display: none;
}

@media only screen and (max-width: 600px) {
  #bounceArrow1 {
    display: none;
  }
  #bounceArrow2 {
    display: block;
    padding-top: 10vh;
  }

  #linkContainer {
    display: none;
  }
  #homeBackground {
    background-color: black;

    font-size: 45px;
    height: 500px;
  }
  .header {
    font-size: 45px;
    padding-top: 25px;
  }

  .layer {
    background-color: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}
