#bioHeader {
  font-size: 86px;
  background-color: black;
  margin-bottom: 0;
  padding-top: 60px;
  padding-left: 60px;
  position: relative;
  text-align: center;
}

#bio {
  font-size: 30px;
  background-color: black;
  margin-bottom: 0;
  padding-top: 60px;
  padding-left: 60px;
  position: relative;
}

#aboutMeHeaderContainer {
  padding-top: 15px;
}

#aboutBackground {
  background-image: url(../../images/FullSize/lol.JPG);

  height: 1005px;

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: 1% 15%;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0;
  padding: 0;
  border: 0;
}
#aboutMeHeaderWrapper {
  background-color: black;
  color: white;
}

#aboutMeHeader {
  padding-top: 30px;
  padding-left: 380px;
  padding-bottom: 15px;
  text-align: left;
  font-size: 90px;
  color: white;
  background-color: black;
  font-family: "Dancing Script", cursive;
}
#aboutMeText1 {
  padding-left: 380px;
  padding-right: 20px;
  text-align: left;
  font-size: 22px;
  color: white;
  background-color: black;
  font-family: "Raleway", sans-serif;
}

#aboutMeText2 {
  padding-left: 20px;
  padding-right: 380px;
  text-align: left;
  font-size: 22px;
  color: white;
  background-color: black;
  font-family: "Raleway", sans-serif;
}

.layer {
  background-color: rgba(29, 29, 29, 0.3);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 600px) {
  #aboutBackground {
    background-color: black;
    background-attachment: fixed;
    background-position: 1% 15%;
    background-repeat: no-repeat;
    background-size: cover;
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 45px;
    height: 500px;
  }
}
